.cardProfile {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    max-width: 300px;
    margin: auto;
    margin-top: 100px;
    text-align: center;
    font-family: arial;
  }
  
  .title {
    color: grey;
    font-size: 18px;
  }
  
  .buttonProfile {
    border: none;
    outline: 0;
    padding: 8px;
    cursor: pointer;
    width: 100px;
    font-size: 18px;
    margin: 10px;
  }
.Img{
    width: 100%;
    height: 250px;
}